.product {
    margin-top: 40px;
}

.buttonGoBack{
    font-size: 16px;
    padding: 5px;
    color: #344050;
    border: 1px #344050 solid;
    text-transform: uppercase;
    letter-spacing: 1px;
    border-radius: 3px;
}

.product_header {
    display: flex;
    justify-content: space-between;
    height: 120px;
}

.product_header_discount {
    display: inline;
    background-color: #F63A3A;
    color: #fff;
    padding: 5px;
}

.product_header_logo {
    max-width: 160px;
    max-height: 80px;
}

.product_body {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.product_body_img {
    max-height: 415px;
}

.product_body_inf {
    display: grid;
    grid-template-areas:
        "name name"
        "stars stars"
        "oldPrice newPrice";
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 120px 55px 110px;
    gap: 20px;
}

.product_body_inf_name {
    grid-area: "name";
}

.product_body_inf_stars {
    grid-area: "stars";
}

.product_body_inf_oldPrice {
    grid-area: "oldPrice";
}

.product_body_inf_newPrice {
    grid-area: "newPrice";
}

.product_footer {}